export function initTheme() {
  const themeSwitch = document.querySelector('#checkbox');
  const savedTheme = localStorage.getItem('theme') || 'dark';

  // Body'ye temayÄ± hemen uygula (Switch'i bekleme)
  if (savedTheme === 'light') {
    document.body.classList.add('light-theme');
  } else {
    document.body.classList.remove('light-theme');
  }

  // EÄŸer switch henÃ¼z DOM'da yoksa (Header yÃ¼klenmemiÅŸ olabilir)
  if (!themeSwitch) {
    // 100ms sonra tekrar kontrol et (Header yÃ¼klenmesi iÃ§in zaman tanÄ±)
    setTimeout(initTheme, 100);
    return;
  }

  themeSwitch.checked = (savedTheme === 'light');

  // Olay dinleyiciyi sadece bir kez ekle
  if (!themeSwitch.dataset.listenerAdded) {
    themeSwitch.addEventListener('change', () => {
      if (themeSwitch.checked) {
        document.body.classList.add('light-theme');
        localStorage.setItem('theme', 'light');
      } else {
        document.body.classList.remove('light-theme');
        localStorage.setItem('theme', 'dark');
      }
    });
    themeSwitch.dataset.listenerAdded = 'true';
  }
}