import axios from 'axios';

// API AyarlarÄ±
const API_KEY = '98ff2d6267ceea8e039422b0f46fb813';
const BASE_URL = 'https://api.themoviedb.org/3';

// Merkezi Axios YapÄ±landÄ±rmasÄ±
const api = axios.create({
  baseURL: BASE_URL,
  params: {
    api_key: API_KEY,
    language: 'en-US', // VarsayÄ±lan dil
  },
});

/**
 * GÃ¼nlÃ¼k veya HaftalÄ±k Trend Filmleri Getirir
 * @param {string} period - 'day' veya 'week' (VarsayÄ±lan: 'day')
 * @returns {Promise<Array>} Film listesi
 */
export const getTrending = async (period = 'day') => {
  try {
    const { data } = await api.get(`/trending/movie/${period}`);
    return data.results; //
  } catch (error) {
    console.error(`Trend filmler (${period}) alÄ±namadÄ±:`, error);
    throw error;
  }
};

/**
 * Anahtar kelime ve yÄ±la gÃ¶re film aramasÄ± yapar (Katalog SayfasÄ± iÃ§in)
 * @param {string} query - Arama terimi
 * @param {number} page - Sayfa numarasÄ±
 * @param {string} year - YayÄ±n yÄ±lÄ± (Opsiyonel)
 */
export const searchMovies = async (query, page = 1, year = '') => {
  try {
    const { data } = await api.get('/search/movie', {
      params: { query, page, year },
    });
    return data; // Sayfalama (pagination) iÃ§in tÃ¼m objeyi dÃ¶ndÃ¼rÃ¼yoruz
  } catch (error) {
    console.error('Film aramasÄ± baÅŸarÄ±sÄ±z:', error);
    throw error;
  }
};

/**
 * Film detaylarÄ±nÄ± getirir (Modal penceresi iÃ§in)
 * @param {number} movieId - Filmin ID'si
 */
export const getMovieDetails = async (movieId) => {
  try {
    const { data } = await api.get(`/movie/${movieId}`);
    return data; //
  } catch (error) {
    console.error('Film detaylarÄ± alÄ±namadÄ±:', error);
    throw error;
  }
};

/**
 * Filmin videolarÄ±nÄ± (Fragman/Trailer) getirir
 * @param {number} movieId - Filmin ID'si
 */
export const getMovieVideos = async (movieId) => {
  try {
    const { data } = await api.get(`/movie/${movieId}/videos`);
    // Genellikle 'YouTube' ve 'Trailer' olan ilk videoyu seÃ§mek iÃ§in filtreleme yapÄ±yoruz
    return data.results.filter(video => video.site === 'YouTube' && video.type === 'Trailer'); //
  } catch (error) {
    console.error('Film fragmanÄ± bulunamadÄ±:', error);
    throw error;
  }
};

/**
 * Gelecek (Upcoming) filmleri getirir
 * @param {number} page - Sayfa numarasÄ±
 */
export const getUpcoming = async (page = 1) => {
  try {
    const { data } = await api.get('/movie/upcoming', { params: { page } });
    return data; //
  } catch (error) {
    console.error('Gelecek filmler alÄ±namadÄ±:', error);
    throw error;
  }
};

/**
 * TÃ¼m film tÃ¼rlerini (Genres) getirir (Filtreleme iÃ§in)
 */
export const getGenres = async () => {
  try {
    const { data } = await api.get('/genre/movie/list');
    return data.genres; //
  } catch (error) {
    console.error('TÃ¼r listesi alÄ±namadÄ±:', error);
    throw error;
  }
};

export default {
  getTrending,
  searchMovies,
  getMovieDetails,
  getMovieVideos,
  getUpcoming,
  getGenres,
};