// footer.js
const openBtn = document.getElementById('openTeamModal');
const closeBtn = document.getElementById('closeTeamModal');
const modal = document.getElementById('teamModal');

// Sadece buton sayfada varsa dinleyici ekle (Hata korumasÄ±)
if (openBtn && modal) {
  openBtn.addEventListener('click', (e) => {
    e.preventDefault();
    modal.classList.add('is-open'); // Stil iÃ§in class kullanmak daha iyidir
    document.body.style.overflow = 'hidden'; // Arka plan kaymasÄ±nÄ± engelle
  });
}

if (closeBtn && modal) {
  closeBtn.addEventListener('click', () => {
    modal.classList.remove('is-open');
    document.body.style.overflow = 'auto';
  });
}

// Modal dÄ±ÅŸÄ±na tÄ±klandÄ±ÄŸÄ±nda kapatma
if (modal) {
  modal.addEventListener('click', (e) => {
    if (e.target === modal) {
      modal.classList.remove('is-open');
      document.body.style.overflow = 'auto';
    }
  });
}

// ESC tuÅŸu ile kapatma (Ekstra konfor)
window.addEventListener('keydown', (e) => {
  if (e.key === 'Escape' && modal && modal.classList.contains('is-open')) {
    modal.classList.remove('is-open');
    document.body.style.overflow = 'auto';
  }
});